<template>
  <div class="login-container">
    <el-form
      :model="ruleForm2"
      :rules="rules2"
      status-icon
      ref="ruleForm2"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-page"
    >
      <h3 class="title" style="text-align:center">系统登录</h3>
      <!-- 用户名 -->
      <el-form-item prop="username">
        <el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="用户名">
          <template slot="prepend">
            <span class="fa fa-user fa-lg" style="width: 13px"></span>
          </template>
        </el-input>
      </el-form-item>
      <!-- 密码 -->
      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="ruleForm2.password"
          auto-complete="off"
          placeholder="密码"
          show-password
        >
          <template slot="prepend">
            <span class="fa fa-lock fa-lg" style="width: 13px"></span>
          </template>
        </el-input>
      </el-form-item>
      <el-form-item prop="verifycode">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-input v-model="ruleForm2.verifycode" placeholder="请输入验证码"></el-input>
          </el-col>
          <el-col :span="6" style="line-height:0px">
            <img :src="verifycode" alt="验证码" style="width:100px; height:40px" @mousedown.prevent />
          </el-col>
          <el-col :span="5" :offset="1">
            <el-button type="text" class="textbtn" @click="getVerifyCode">换一张</el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-checkbox v-model="checked" class="rememberme">记住密码</el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      logining: false,
      checked: false,
      ruleForm2: {
        username: "",
        password: "",
        verifycode: ""
      }, //验证规则
      rules2: {
        username: [
          {
            required: true,
            message: "please enter your account",
            trigger: "blur"
          }
        ],
        password: [
          { required: true, message: "enter your password", trigger: "blur" }
        ]
      },
      verifycode: ""
    };
  },
  // 页面载入后读取Storage
  created() {
    //this.getCookie();
    sessionStorage.setItem('isLogin',false);
    this.ruleForm2.username = localStorage.getItem("USER");
    this.ruleForm2.password = localStorage.getItem("PWD");
    this.getVerifyCode();
  },
  methods: {
    handleSubmit(event) {
      this.$refs.ruleForm2.validate(valid => {
        if (valid) {
          this.logining = true;
          var loginParams = {
            username: this.ruleForm2.username,
            password: this.ruleForm2.password
            //verifycode: this.ruleForm2.verifycode
          };
          this.$api.requestLogin(qs.stringify(loginParams)).then(data => {
            this.logining = false;
            //console.log(data);
            let { success, errCode, errMsg } = data;
            if (!success) {
              this.$message({
                message: errMsg,
                type: "error"
              });
              this.$router.go(0);
            } else {
              // 登陆成功 保存帐号密码
              if (this.checked) {
                localStorage.setItem("USER", this.ruleForm2.username);
                localStorage.setItem("PWD", this.ruleForm2.password);
              }
              localStorage.setItem("USERTK", data.data);
              sessionStorage.setItem("isLogin", true);
              this.$router.push({ path: "/" });
            }
          });
        } else {
          this.$alert("用户名或密码错误!", "错误", {
            confirmButtonText: "ok"
          });
        }
      });
    },
    getVerifyCode() {
      this.$api.getVerifyCode().then(data => {
        this.verifycode = "data:image/gif;base64," + data;
      });
    },
    setCookie(name, pass, days) {
      let expire = new Date();
      expire.setDate(expire.getDate() + days);
      document.cookie = `C-username=${name};expires=${expire}`;
      document.cookie = `C-password=${pass};expires=${expire}`;
    },
    getCookie() {
      if (document.cookie.length) {
        let arr = document.cookie.split("; ");
        for (let i = 0; i < arr.length; i++) {
          let arr2 = arr[i].split("=");
          if (arr2[0] === "C-username") {
            this.ruleForm2.username = arr2[1];
          } else if (arr2[0] === "C-password") {
            this.ruleForm2.password = arr2[1];
            this.rememberme = true;
          }
        }
      }
    },
    deleteCookie() {
      this.setCookie("", "", -1);
    }
  }
};
</script>

<style scoped>
.login-container {
  width: 100%;
  height: 100%;
}
.login-page {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
label.el-checkbox.rememberme {
  margin: 0px 0px 15px;
  text-align: left;
  float: right;
}
</style>



